.inner,
.inner-lg,
.inner-lg-pl {
  max-width: 1340px;
  width: 100%;
  margin: 0 auto;
  padding: 0 30px;
  @include lg {
    padding: 0 60px; }
  @include md {
    padding: 0 30px; } }
.inner-lg-pl {
  padding-left: 148px;
  @include lg {
    padding-left: 10.9385%; } }
.inner {
  padding: 0 150px;
  @include lg {
    padding: 0 10.9385%; } }

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -60px; }

.col {
  @include flex-size(50%);
  padding-left: 60px;
  padding-right: 60px; }
