  animation: lineAppearLeft .3s ease-out .1s forwards;
}

.header .menu-item {
  transform: translateY(-20px);
}

@media only screen and (max-width: 767px) {
  .header .menu-item {
    padding-left: 0;
  }
}

.menu-item:before {
  counter-increment: number;
  content: "0" counter(number);
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  font-size: 14px;
}

@media only screen and (max-width: 767px) {
  .header .menu-item:before {
    display: none;
  }
}

.menu-item__line-wrap {
  position: absolute;
  top: 50%;
  right: 0;
  width: 40px;
  height: 2px;
}

@media only screen and (max-width: 1023px) {
  .header .menu-item__line-wrap {
    right: -30px;
  }
}

.menu-item__line {
  position: absolute;
  top: 50%;
  right: 0;
  width: 40px;
  overflow: hidden;
}

.menu-item__line-inner {
  display: block;
  width: 100%;
  height: 1px;
  background-color: #F03454;
  transform: translateX(-100%);
  animation: lineHideRight 0.3s cubic-bezier(0.8, 0, 0.2, 1) forwards;
}

.menu-item__link {
  color: inherit;
}

.no-touch .menu-item__link:hover {
  color: #817D8E;
}

@keyframes lineHideRight {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(100%);
  }
}

@keyframes lineAppearLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

